$font: 'Lucida Console';
$font-size: 18px;
$primary: rgb(0, 0, 168);
$secondary: rgb(168, 168, 168);
$success: rgb(0, 168, 0);
$danger: rgb(168, 0, 0);
$warning: rgb(168, 168, 0);
$info: rgb(0, 168, 168);

.main {
  height: 100vh;
}

.modal-dialog {
  display: flex;
  margin: 50px auto 0 auto;
}

.btn-group {
  display: flex;
  flex-direction: column;
}